.banner {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  &>img{
    width: 100%;
  }
  &>section{
    position: absolute;
    text-align: right;
    left: 50%;
    @include T{
      right: 38px;
      left: auto;
    }
    @include M{
      right: 15px;
      left: auto;
      h4 {
        font-size: 16px;
      }
    }
  }
}

[data-query="P"] {
  display: block;
}

[data-query="M"] {
  display: none;
}

.mFrame.profession,
.mFrame.contact {
  background-color: var(--secondary);
}

.iconCard {
  text-align: center;
  padding: 0px 15px;
  width: 25%;
  @include T{
    width: 50%;
  }
  @include M{
    width: 100%;
  }
}

.iconCardTitle {
  color: var(--primary-main);
  margin-bottom: 8px;
}

.iconCardIcon {
  fill: var(--primary-main);
  width: 83px;
  height: 83px;
  margin-bottom: 24px;
}

.iconCardContent {
  text-align: left;
}

.horizentalCard {
  display: flex;
  align-items: center;
  @include M{
    position: relative;
  }
  &:not(:last-child){
    margin-bottom: 32px;
  }
}

.horizentalCardImg {
  width: 60%;
  @include T{
    width: 55%;
  }
  @include T{
    width: 100%;
  }
}

.horizentalCardContent {
  text-transform: uppercase;
  color: var(--primary-light);
  margin: 0 2rem;
  display: flex;
  align-items: center;
  @include M{
    margin: 0;
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    font-size: 28px;
  }
  &::before {
    content: "";
    margin-right: 22px;
    width: 44px;
    height: 44px;
    background-repeat: no-repeat;
    background-size: contain;
  }
}

.seriesList > .horizentalCard:nth-child(2) {
  flex-direction: row-reverse;
  @include T{
    flex-direction: row;
  }
}

.seriesList > .horizentalCard:nth-child(1) > .horizentalCardContent::before {
  background-image: url("https://hexschool.github.io/webLayoutTraining1st/week3/icon-diamond.svg");
}
.seriesList > .horizentalCard:nth-child(2) > .horizentalCardContent::before {
  background-image: url("https://hexschool.github.io/webLayoutTraining1st/week3/icon-sun.svg");
}
.seriesList > .horizentalCard:nth-child(3) > .horizentalCardContent::before {
  background-image: url("https://hexschool.github.io/webLayoutTraining1st/week3/icon-thunder.svg");
}

.mFrame.cobrand {
  background-image: url("https://hexschool.github.io/webLayoutTraining1st/week3/bg.png");
  background-position: center;
  background-repeat: no-repeat;
}

.list.cobrand > li {
  width: 50%;
  padding: 0px 15px;
  @include M{
    width: 100%;
    padding: 0px;
    margin-bottom: 16px;
  }
}

.cobrandCard {
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  &::before {
    content: "";
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    background-color: rgba(255, 255, 255, 0.5);
  }
}

.cobrandCardImg {
  max-width: 100%;
}

.cobrandCardContent {
  position: absolute;
  color: var(--primary-main);
  @include M{
    font-size: 32px;
  }
}

.list.commend > li {
  width: 25%;
  padding: 0px 15px;
  margin-bottom: 32px;
  @include T{
    width: 50%;
  }
  @include M{
    width: 100%;
  }
}

.commentCard {
  box-shadow: 0px 2px 6px rgba(0, 0, 0, 0.1);
  box-sizing: border-box;
  background-color: #fff;
  border-radius: 4px;
  display: flex;
  flex-direction: column;
  height: 100%;
}

.commentCardImg {
  max-width: 100%;
}

.commentCardContent {
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding: 16px;
  &> footer {
    margin-top: 52px;
  }
}

.mFrameBd.contact p {
  margin-bottom: 20px;
}

.formGroup {
  display: flex;
  align-items: center;
}

.formLabel {
  display: block;
  margin-bottom: 4px;
}

.formInput {
  border: 1px solid black;
  padding: 12px 16px 14px;
  display: block;
  margin-bottom: 16px;
  width: 100%;
}

.formCheckbox {
  width: 24px;
  height: 24px;
  margin-right: 4px;
  & ~ label {
    display: inline;
    margin-bottom: 0;
  }
}

.contactFormBtn {
  background-color: var(--primary-light);
  padding: 16px;
  color: #fff;
  width: 40%;
  margin: 0 auto;
  margin-top: 40px;
  display: block;
  text-align: center;
  cursor: pointer;
  border: none;
  transition: var(--anim-time);
  &:hover{
    background-color: var(--primary-dark);
  }
}

[data-query="P"] {
  @include M{
    display: none;
  }
}

[data-query="M"] {
  @include M{
    display: block;
  }
}