.qa{
  h3{
    font-size: 32px;
    margin-bottom: 36px;
    @include M{
      font-size: 20px;
      margin-bottom: 24px;
    }
  }
  ul {
    counter-reset: num;
    li{
      margin-bottom: 32px;
      @include M{
        margin-bottom: 20px;
      }
      h6{
        margin-bottom: 16px;
        @include M{
          margin-bottom: 8px;
        }
        &::before{
          content: 'Q' counter(num) '.';
          counter-increment: num; 
        }
      }
      p{
        margin-left: 6rem;
        @include T{
          margin-left: 4rem;
        }
        @include M{
          margin-left: 0;
        }
      }
    }
  }
}
