.navHeader {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: var(--primary-light);
  padding: 30px 88px;
  position: fixed;
  width: 100%;
  box-sizing: border-box;
  top: 0;
  z-index: 1;
  @include T{
    padding: 30px 40px;
  }
  @include M{
    display: block;
    padding: 0;
    .logo{
      padding: 10px;
      text-align: center;
    }
    .nav{
      flex-wrap: wrap;
      &> li {
        border: 1px solid white;
        width: 50%;
        padding: 8px;
        text-align: center;
        margin: 0;
      }
    }
  }
}

.logo {
  &>h1 {
    position: absolute;
    left: -10000px;
  }
  img { 
    width: 80px;
  }
}

.nav {
  display: flex;
  color: #fff;
  &>li{
    margin: 0 32px;
    @include T{
      margin: 0 20px;
    }
    &>a{
      color: #fff;
      transition: var(--anim-time);
      &:hover{
        color: rgba(255, 255, 255, 0.5);
      }
    }
  }
}

.main {
  margin-top: 108px;
  @include M{
    margin-top: 136px;
  }
}

.mFrameHd {
  font-weight: 700;
  text-align: center;
  margin-bottom: 40px;
}

.mFrame {
  padding-top: 60px;
  padding-bottom: 80px;
  @include T{
    padding-top: 40px;
    padding-bottom: 56px;
  }
  @include M{
    padding-top: 32px;
    padding-bottom: 40px;
  }
}

.list {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  &.footerNav{
    @include M{
      position: absolute;
      right: 0;
      &>.list:first-child{
        display: none;
      }
      img {
        width: 24px;
        height: 24px;
      }
    }
    a{
      transition: var(--anim-time);
      &:hover{
        color: rgba(255, 255, 255, 0.5);
      }
    }
  }
  &.copywriting{
    @include M{
      font-size: 14px;
      small{
        margin-bottom: 8px;
      }
      .list{
        flex-direction: column;
        align-items: flex-start;
        &>li:not(:last-child){
          padding: 0;
          margin-bottom: 8px;
        }
      }
    }
    a{
      transition: var(--anim-time);
      &:hover{
        color: rgba(255, 255, 255, 0.5);
      }
    }
  }
}

.footer {
  padding: 32px;
  background-color: var(--primary-light);
  color: white;
  @include M{
    padding: 24px 15px;
    &>.container{
      position: relative;
    }
  }
  .list{
    align-items: center;
    &>li:not(:last-child) {
      padding-right: 24px;
    }
  }
  a{
    color: #fff;
  }
}

.contactInfo {
  font-size: 36px;
  line-height: 1.5;
  margin-top: 24px;
  margin-bottom: 32px;
  padding-bottom: 24px;
  border-bottom: 1px solid white;
  @include M{
    padding-bottom: 24px;
    margin-bottom: 16px;
    margin-top: 0;
    font-size: 16px;
  }
  &>li>a{
    display: flex;
    align-items: center;
  }
  a::before {
    content: "";
    width: 36px;
    height: 36px;
    margin-right: 22px;
    background-position: center;
    background-repeat: no-repeat;
    background-size: contain;
    @include M{
      width: 24px;
      height: 24px;
      margin-right: 8px;
    }
  }
}

.contactInfo li:first-child {
  @include M{
    margin-bottom: 8px;
  }
  a::before {
  background-image: url("https://hexschool.github.io/webLayoutTraining1st/week3/icon-phone.svg");
  }
}
.contactInfo li:last-child a::before {
  background-image: url("https://hexschool.github.io/webLayoutTraining1st/week3/icon-mail.svg");
}

.mArticle{
  display: flex;
  flex-direction: column;
  margin-bottom: 128px;
  @include T{
    display: flex;
    flex-direction: column;
    margin-bottom: 96px;
  }
  @include M{
    display: flex;
    flex-direction: column;
    margin-bottom: 56px;
  }
}
.mArticleHd{
  margin-top: 96px;
  margin-bottom: 60px;
  @include M{
    margin-top: 28px;
    margin-bottom: 24px;
  }
  h2 {
    font-size: 48px;
    display: flex;
    align-items: center;
    &::before{
      content: "";
      width: 8px;
      height: 70px;
      display: inline-block;
      margin-right: 16px;
      background-color: var(--primary-main);
    }
  }
}
.tabs{
  display: flex;
  justify-content: center;
}
.tab{
  width: 30%;
  min-width: 120px;
  max-width: 350px;
  border: 1px solid var(--light-default);
  position: relative;
  font-size: 32px;
  &::after{
    content: "";
    width: calc(100% + 2px);
    height: 0;
    background-color: var(--primary-light);
    position: absolute;
    bottom: 0;
  }
  &.act::after{
    height: 8px;
    @include T{
      height: 4px;
    }
  }
  a{
    padding: 26px 8px;
    font-weight: 700;
    display: flex;
    align-items: center;
    justify-content: center;
    color: black;
    transition: var(--anim-time);
    &:hover{
      background-color: var(--light-default);
    }
    @include T{
      padding: 14px 8px;
      font-size: 24px;
    }
    @include M{
      padding: 6px 8px;
      font-size: 14px;
    }
    &::before{
      content: "";
      margin-right: 8px;
      width: 48px;
      height: 48px;
      display: inline-block;
      background-size: contain;
      background-position: center;
      background-repeat: no-repeat;
      flex-shrink: 0;
      @include T{
        width: 32px;
        height: 32px;
      }
      @include M{
        width: 16px;
        height: 16px;
      }
    }
  }
  .optional::before{
    background-image: url("https://hexschool.github.io/webLayoutTraining1st/week3/icon-diamond-black.svg");
  }
  .sunglasses::before{
    background-image: url("https://hexschool.github.io/webLayoutTraining1st/week3/icon-sun-black.svg");
  }
  .functional::before{
    background-image: url("https://hexschool.github.io/webLayoutTraining1st/week3/icon-thunder-black.svg");
  }
}
.mTwoBanner{
  display: flex;
}

.pagination {
  display: flex;
  justify-content: center;
  &>a {
    transition: var(--anim-time);
    padding: 12px 16px;
    display: flex;
    justify-content: center;
    align-items: center;
    border: 1px solid black;
    border-right: 0px;
    color: black;
    &:hover{
      background-color: black;
      color: white;
    }
    &:last-child{
      border-right: 1px solid black;
    }
  }
}
