.blogNav{
  background-color: var(--primary-main);
}

.list.post{
  ul {
    display: flex;
  }
  li{
    margin: 12px;
    @include M{
      margin: 16px 10px;
    }
    &:first-child{
      margin-left: 0;
    }
  }
  a{
    font-weight: 700;
    color: #fff;
    &:hover{
      opacity: rgba(255, 255, 255, 0.5);
    }
  }
}

.breadCrumb{
  display: flex;
  margin: 8px 0;
  li:not(:last-child)::after{
    content: "/";
    display: inline-block;
    margin: 0px 4px;
  }
  a{
    color: black;
  }
  [data-location="true"]{
    color: var(--primary-main);
  }
}

.tag {
  font-size: 24px;
  font-weight: 700;
  color: var(--primary-light);
  @include T{
    font-size: 16px;
  }
  @include M{
    font-size: 14px;
  }
  &.list{
    justify-content: flex-start;
    li {
      padding-right: 24px;
    }
  }
}

.blogBd {
  display: flex;
  margin-bottom: 150px;
  @include T{
    margin-bottom: 96px;
  }
  @include M{
    margin-bottom: 56px;
    flex-direction: column;
  }
  &>article {
    width: 75%;
    margin-right: 2rem;
    @include M{
      width: 100%;
      margin-right: 0;
    }
  }
  &>aside {
    width: 23%;
    color: var(--primary-light);
    @include M{
      width: 100%;
    }
  }
  .blogBdImg{
    width: 100%;
  }
}

.blogArticleMain{
  color: var(--default);
  font-size: 20px;
  p{
    margin-top: 24px;
    margin-bottom: 40px;
  }
}

.tagsHd {
  font-size: 28px;
  font-weight: 700;
  margin-bottom: 8px;
  display: flex;
  align-items: center;
  @include T{
    font-size: 20px;
  }
  &::before {
    content: "";
    width: 38px;
    height: 28px;
    display: inline-block;
    margin-right: 12px;
    background-image: url("https://hexschool.github.io/webLayoutTraining1st/week3/icon-tag.svg");
    background-size: cover;
    background-position: center;
  }
}

.subtitle {
  @include T{
    &>p {
      font-size: 14px;
    }
  }
  @include M{
    margin-bottom: 20px;
    &>p {
      font-size: 14px;
    }
  }
  &h3{
    font-size: 28px;
    @include T{
      font-size: 16px;
    }
    @include M{
      font-size: 14px;
    }
  }
}

.postPagenation{
  margin-top: 24px;
  display: flex;
  justify-content: space-between;
  @include T{
    margin-top: 16px;
  }
  @include M{
    margin-top: 10px;
    margin-bottom: 30px;
    flex-direction: column;
  }
}

.pagenationLabel{
  display: flex;
  align-items: center;
  font-size: 20px;
  color: black;
  transition: var(--anim-time);
  position: relative;
  @include T{
    font-size: 16px;
  }
  @include M{
    font-size: 14px;
  }
  &:hover{
    font-weight: 700;
  }
  img{
    display: inline-block;
    margin-right: 12px;
  }
  &:nth-child(2){
    flex-direction: row-reverse;
    @include M{
      flex-direction: row;
    }
  }
}

.shareMedia {
  padding-bottom: 20px;
  border-bottom: 1px solid black;
  @include T{
    font-size: 16px;
  }
  a {
    color: black;
    font-weight: 700;
    font-size: 20px;
    margin-right: 16px;
  }
}

.blogTitle{
  @include T{
    font-size: 32px;
  }
  @include M{
    font-size: 20px;
    color: var(--primary-main);
  }
}

[data-query="PT"]{
  @include M{
    display: none;
  }
}