.blogCardWrap{
  text-align: left;
  border: 0px;
  background: transparent;
  margin-bottom: 3rem;
  cursor: pointer;
  border-radius: 4px;
  padding: 0;
  transition: var(--anim-time);
  margin-bottom: 3rem;
  &:hover {
    transform: translateY(-5px) scale(1.01);
  }
}
.blogCard{
  display: flex;
  @include M{
    flex-direction: column;
    align-items: stretch;
    margin-bottom: 20px;
  }
}
.blogCardImg{
  align-self: start;
  @include T{
    width: 40%;
  }
  @include M{
    margin-bottom: 8px;
  }
}
.blogCardCnt{
  margin-left: 36px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  @include T{
    margin-right: 30px;
  }
  @include M{
    margin-left: 0;
  }
}
.blogCardCntBd{
  &>h6{
    font-size: 32px;
    color: var(--primary-main);
    @include T{
      font-size: 20px;
    }
  }
  &>p{
    @include T{
      font-size: 14px;
      position: relative;
    }
    @include M{
      display: none;
    }
  }
  [data-query="T"]{
    @include T{
      display: inline-block;
      margin-left: 8px;
      font-weight: 700;
      font-size: 14px;
    }
  }
}
.subtitle {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 24px;
  font-weight: 600;
  @include T{
    font-size: 14px;
    margin-bottom: 12px;
    &>h6{
      font-size: 16px;
    }
  }
  @include M{
    margin-bottom: 0;
  }
}
[data-query="T"]{
  display: none;
}
.blogCardAction {
  font-weight: 900;
  font-size: 36px;
  text-align: right;
  margin-top: 14px;
  color: var(--primary-main);
  @include T{
    display: none;
  }
  @include M{
    display: none;
  }
}
